<template>
  <v-container>
    <v-row justify="center">
      <v-col cols="12" md="10">
        <v-card>
          <v-data-table
            :headers="headers"
            :items="tasks"
            item-key="id"
            class="elevation-1"
            :loading="loading"
            @click:row="handleRowClick"
            hover
          >
            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.title="{ item }">
              {{ item.title }}
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.description="{ item }">
              <div class="text-truncate" style="max-width: 200px;">
                {{ item.description }}
              </div>
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.assignments.worker="{ item }">
              <v-menu v-if="item.assignments && item.assignments.length > 0" @click.stop>
                <template #activator="{ props }">
                  <v-btn v-bind="props" color="green" size="small" variant="tonal">
                    {{ item.assignments[0]?.user?.name || '–ë–µ–∑ –∏–º–µ–Ω–∏' }}
                    <v-badge 
                      v-if="item.assignments.length > 1" 
                      :content="item.assignments.length" 
                      color="primary"
                      inline
                    />
                  </v-btn>
                </template>

                <v-list density="compact">
                  <v-list-item
                    v-for="assignment in item.assignments"
                    :key="assignment.id"
                  >
                    <template #prepend>
                      <v-avatar size="24" :color="getRoleColor(assignment.user?.role)">
                        <v-icon size="16">mdi-account</v-icon>
                      </v-avatar>
                    </template>
                    <v-list-item-title class="text-body-2">
                      {{ assignment.user?.name || '–ë–µ–∑ –∏–º–µ–Ω–∏' }}
                    </v-list-item-title>
                    <template #append>
                      <v-chip size="x-small" :color="getRoleColor(assignment.user?.role)">
                        {{ assignment.user?.role }}
                      </v-chip>
                    </template>
                  </v-list-item>
                </v-list>
              </v-menu>
              
              <v-chip v-else color="grey" variant="outlined" size="small">
                –ù–µ –Ω–∞–∑–Ω–∞—á–µ–Ω
              </v-chip>
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.status="{ item }">
              <v-chip
                :color="getStatusColor(item?.status)"
                variant="flat"
                size="small"
              >
                {{ getStatusText(item?.status) }}
              </v-chip>
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.createdBy.name="{ item }">
              <div class="d-flex align-center">
                <v-avatar size="24" :color="getRoleColor(item.createdBy?.role)" class="mr-2">
                  <v-icon size="16">mdi-account</v-icon>
                </v-avatar>
                <span>{{ item.createdBy?.name || '–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–æ' }}</span>
                <v-chip 
                  v-if="item.createdBy?.id === auth.user?.id" 
                  color="primary" 
                  size="x-small" 
                  class="ml-2"
                >
                  –í–∞—à–∞
                </v-chip>
              </div>
            </template>

            <!-- eslint-disable-next-line vue/valid-v-slot -->
            <template #item.actions="{ item }">
              <div class="d-flex ga-2" @click.stop>
                <v-btn
                  v-if="canDeleteTask(item)"
                  color="error"
                  size="small"
                  variant="outlined"
                  @click="confirmDelete(item)"
                  :loading="deletingTasks.includes(item.id)"
                  :disabled="loading"
                >
                  –£–¥–∞–ª–∏—Ç—å
                </v-btn>
                <v-chip 
                  v-if="!canEditTask(item) && !canDeleteTask(item)"
                  color="grey"
                  variant="outlined"
                  size="small"
                >
                  –¢–æ–ª—å–∫–æ –ø—Ä–æ—Å–º–æ—Ç—Ä
                </v-chip>
              </div>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
    </v-row>

    <!-- –î–∏–∞–ª–æ–≥ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è -->
    <v-dialog v-model="showDeleteDialog" max-width="400px">
      <v-card>
        <v-card-title class="text-h6">
          –ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ —É–¥–∞–ª–µ–Ω–∏–µ
        </v-card-title>
        <v-card-text>
          –í—ã —É–≤–µ—Ä–µ–Ω—ã, —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –∑–∞–¥–∞—á—É "{{ taskToDelete?.title }}"?
          –≠—Ç–æ –¥–µ–π—Å—Ç–≤–∏–µ –Ω–µ–ª—å–∑—è –æ—Ç–º–µ–Ω–∏—Ç—å.
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn variant="text" @click="showDeleteDialog = false">
            –û—Ç–º–µ–Ω–∞
          </v-btn>
          <v-btn 
            color="error" 
            @click="deleteTask"
            :loading="deletingTasks.includes(taskToDelete?.id)"
          >
            –£–¥–∞–ª–∏—Ç—å
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- –î–∏–∞–ª–æ–≥ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∑–∞–¥–∞—á–∏ -->
    <v-dialog v-model="showEditDialog" max-width="700px">
      <v-card v-if="editingTask">
        <v-card-title>
          <span class="text-h6">–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –∑–∞–¥–∞—á—É</span>
          <v-chip 
            v-if="editingTask?.createdBy?.id === auth.user?.id && !isAdmin" 
            color="primary" 
            size="small" 
            class="ml-3"
          >
            –í–∞—à–∞ –∑–∞–¥–∞—á–∞
          </v-chip>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field 
                  v-model="editingTask.title" 
                  label="–ù–∞–∑–≤–∞–Ω–∏–µ –∑–∞–¥–∞—á–∏" 
                  required 
                  :rules="[v => !!v || '–ù–∞–∑–≤–∞–Ω–∏–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ']"
                  variant="outlined"
                />
              </v-col>
              
              <v-col cols="12">
                <v-textarea 
                  v-model="editingTask.description" 
                  label="–û–ø–∏—Å–∞–Ω–∏–µ –∑–∞–¥–∞—á–∏" 
                  required 
                  :rules="[v => !!v || '–û–ø–∏—Å–∞–Ω–∏–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ']"
                  rows="3"
                  variant="outlined"
                />
              </v-col>

              <v-col cols="12" md="6">
                <v-select
                  v-model="editingTask.status"
                  :items="statusOptions"
                  item-title="label"
                  item-value="value"
                  label="–°—Ç–∞—Ç—É—Å –∑–∞–¥–∞—á–∏"
                  variant="outlined"
                />
              </v-col>

              <v-col cols="12" md="6">
                <v-chip 
                  :color="getStatusColor(editingTask.status)"
                  class="mt-4"
                >
                  {{ getStatusText(editingTask.status) }}
                </v-chip>
              </v-col>

              <v-col cols="12">
                <v-select
                  v-model="editingTask.workerIds"
                  :items="users"
                  item-title="name"
                  item-value="id"
                  label="–ù–∞–∑–Ω–∞—á–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π"
                  multiple
                  chips
                  closable-chips
                  :loading="loadingUsers"
                  variant="outlined"
                >
                  <template #selection="{ item, index }">
                    <v-chip
                      v-if="index < 3"
                      size="small"
                      closable
                      :color="getRoleColor(item.raw.role)"
                      variant="tonal"
                      @click:close="removeUser(item.raw.id)"
                    >
                      <v-icon start size="16">mdi-account</v-icon>
                      {{ item.title }}
                    </v-chip>
                    <span
                      v-if="index === 3"
                      class="text-grey text-caption align-self-center ml-2"
                    >
                      (+{{ editingTask.workerIds.length - 3 }} –¥—Ä—É–≥–∏—Ö)
                    </span>
                  </template>

                  <template #item="{ props, item }">
                    <v-list-item v-bind="props" :title="item.title">
                      <template #prepend>
                        <v-avatar size="32" :color="getRoleColor(item.raw.role)">
                          <v-icon>mdi-account</v-icon>
                        </v-avatar>
                      </template>
                      <template #append>
                        <v-chip size="x-small" :color="getRoleColor(item.raw.role)">
                          {{ item.raw.role }}
                        </v-chip>
                        <v-icon 
                          v-if="editingTask.workerIds.includes(item.value)" 
                          color="success"
                          class="ml-2"
                        >
                          mdi-check
                        </v-icon>
                      </template>
                    </v-list-item>
                  </template>

                  <template #no-data>
                    <v-list-item>
                      <v-list-item-title>–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω—ã</v-list-item-title>
                    </v-list-item>
                  </template>
                </v-select>
              </v-col>

              <v-col cols="12" v-if="editingTask.workerIds.length === 0">
                <v-alert 
                  type="warning" 
                  variant="tonal"
                  density="compact"
                >
                  –ó–∞–¥–∞—á–∞ –Ω–µ –Ω–∞–∑–Ω–∞—á–µ–Ω–∞ –Ω–∏ –æ–¥–Ω–æ–º—É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é
                </v-alert>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn variant="text" @click="closeEditDialog">
            –û—Ç–º–µ–Ω–∞
          </v-btn>
          <v-btn 
            color="success" 
            @click="saveEditTask" 
            :loading="isAddingTask"
            :disabled="!editingTask.title || !editingTask.description"
          >
            –°–æ—Ö—Ä–∞–Ω–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏—è
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- –£–≤–µ–¥–æ–º–ª–µ–Ω–∏—è -->
    <v-snackbar
      v-model="showSuccess"
      color="success"
      timeout="3000"
    >
      {{ successMessage }}
    </v-snackbar>

    <v-snackbar
      v-model="showError"
      color="error"
      timeout="5000"
    >
      {{ errorMessage }}
    </v-snackbar>
  </v-container>
</template>

<script setup>
import { usePermissions } from '../../composables/usePermissions.js'
import { useAuthStore } from '../../stores/auth.js'
import { ref, defineProps, toRef, onMounted, computed } from 'vue'
import { taskApi } from '../../api/task.api.js'
import { userApi } from '../../api/user.api.js'

const { isAdmin } = usePermissions()
const auth = useAuthStore()

// Computed –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –ø—Ä–∞–≤ –Ω–∞ –∑–∞–¥–∞—á—É
const canEditTask = computed(() => (task) => {
  return isAdmin.value || task.createdBy?.id === auth.user?.id
})

const canDeleteTask = computed(() => (task) => {
  return isAdmin.value || task.createdBy?.id === auth.user?.id
})

const statusOptions = [
  { label: '–û–∂–∏–¥–∞–µ—Ç', value: 'pending' },
  { label: '–í –ø—Ä–æ—Ü–µ—Å—Å–µ', value: 'in_progress' },
  { label: '–ó–∞–≤–µ—Ä—à–µ–Ω–æ', value: 'completed' }
]

// Props
const props = defineProps({
  tasks: Array,
  getTasks: Function
})

const tasks = toRef(props, 'tasks')
const getTasks = props.getTasks

// Reactive data
const users = ref([])
const loading = ref(false)
const deletingTasks = ref([])
const showEditDialog = ref(false)
const showDeleteDialog = ref(false)
const isAddingTask = ref(false)
const editingTask = ref(null)
const taskToDelete = ref(null)
const loadingUsers = ref(false)

// Notifications
const showSuccess = ref(false)
const showError = ref(false)
const successMessage = ref('')
const errorMessage = ref('')

// Utility functions for notifications
const showSuccessMessage = (message) => {
  successMessage.value = message
  showSuccess.value = true
}

const showErrorMessage = (message) => {
  errorMessage.value = message
  showError.value = true
}

// Load users
async function getUsers() {
  try {
    loadingUsers.value = true
    const response = await userApi.getAll()
    const data = await response.json()
    users.value = data.data.users
  } catch (error) {
    console.error('Error fetching users:', error)
    showErrorMessage('–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π')
  } finally {
    loadingUsers.value = false
  }
}

onMounted(getUsers)

// Table headers
const headers = [
  { title: '–ù–∞–∑–≤–∞–Ω–∏–µ', key: 'title' },
  { title: '–û–ø–∏—Å–∞–Ω–∏–µ', key: 'description' },
  { title: '–°—Ç–∞—Ç—É—Å', key: 'status' },
  { title: '–°–æ–∑–¥–∞—Ç–µ–ª—å', key: 'createdBy.name' },
  { title: '–ò—Å–ø–æ–ª–Ω–∏—Ç–µ–ª–∏', key: 'assignments.worker' },
  { title: '–î–µ–π—Å—Ç–≤–∏—è', key: 'actions', sortable: false },
]

// Dialog functions
function handleRowClick(event, { item }) {
  openEditDialog(item)
}

function openEditDialog(task) {
  editingTask.value = {
    ...task,
    workerIds: task.assignments?.map(assignment => assignment.userId) || []
  }
  showEditDialog.value = true
}

function closeEditDialog() {
  showEditDialog.value = false
  editingTask.value = null
}

function confirmDelete(task) {
  taskToDelete.value = task
  showDeleteDialog.value = true
}

function removeUser(userId) {
  editingTask.value.workerIds = editingTask.value.workerIds.filter(id => id !== userId)
}

// API functions
async function removeTaskAssignments(taskId, currentAssignments) {
  for (const assignment of currentAssignments) {
    const response = await taskApi.removeAssignment(assignment.id)
    if (!response.ok) {
      throw new Error(`–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –Ω–∞–∑–Ω–∞—á–µ–Ω–∏—è ${assignment.id}`)
    }
  }
}

async function saveEditTask() {
  if (!editingTask.value.title || !editingTask.value.description || !editingTask.value.status) {
    showErrorMessage('–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –∑–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–µ –ø–æ–ª—è')
    return
  }

  try {
    isAddingTask.value = true
    
    // Update task
    const taskResponse = await taskApi.edit(editingTask.value.id, {
      title: editingTask.value.title,
      description: editingTask.value.description,
      status: editingTask.value.status
    })

    if (!taskResponse.ok) {
      throw new Error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ –∑–∞–¥–∞—á–∏')
    }

    // Remove old assignments
    if (editingTask.value.assignments?.length > 0) {
      await removeTaskAssignments(editingTask.value.id, editingTask.value.assignments)
    }

    // Add new assignments
    for (const userId of editingTask.value.workerIds) {
      const assignResponse = await taskApi.assignUser({
        taskId: editingTask.value.id,
        userId: userId,
      })
      
      if (!assignResponse.ok) {
        throw new Error(`–û—à–∏–±–∫–∞ –Ω–∞–∑–Ω–∞—á–µ–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è`)
      }
    }

    await getTasks()
    closeEditDialog()
    showSuccessMessage('–ó–∞–¥–∞—á–∞ —É—Å–ø–µ—à–Ω–æ –æ–±–Ω–æ–≤–ª–µ–Ω–∞')
    
  } catch (error) {
    console.error('Error saving task:', error)
    showErrorMessage(error.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–∏ –∑–∞–¥–∞—á–∏')
  } finally {
    isAddingTask.value = false
  }
}

async function deleteTask() {
  if (!taskToDelete.value) return

  try {
    deletingTasks.value.push(taskToDelete.value.id)
    const response = await taskApi.delete(taskToDelete.value.id)
    
    if (!response.ok) {
      throw new Error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –∑–∞–¥–∞—á–∏')
    }
    
    await getTasks()
    showSuccessMessage('–ó–∞–¥–∞—á–∞ —É—Å–ø–µ—à–Ω–æ —É–¥–∞–ª–µ–Ω–∞')
    
  } catch (error) {
    console.error('Error deleting task:', error)
    showErrorMessage(error.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –∑–∞–¥–∞—á–∏')
  } finally {
    deletingTasks.value = deletingTasks.value.filter(id => id !== taskToDelete.value.id)
    showDeleteDialog.value = false
    taskToDelete.value = null
  }
}

// Utility functions
function getRoleColor(role) {
  switch (role) {
    case 'admin': return 'red'
    case 'manager': return 'blue'
    case 'worker': return 'green'
    default: return 'grey'
  }
}

function getStatusColor(status) {
  switch (status) {
    case 'completed': return 'green'
    case 'in_progress': return 'blue'
    case 'pending': return 'orange'
    default: return 'grey'
  }
}

function getStatusText(status) {
  switch (status) {
    case 'completed': return '–ó–∞–≤–µ—Ä—à–µ–Ω–æ'
    case 'in_progress': return '–í –ø—Ä–æ—Ü–µ—Å—Å–µ'  
    case 'pending': return '–û–∂–∏–¥–∞–µ—Ç'
    default: return status
  }
}
</script>