<template>
  <v-container>
    <v-row justify="center">
      <v-col v-if="!isAdmin" cols="10" md="5">
        <v-card>
          <v-card-title>–°–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞—Ç—å –∫–ª—é—á</v-card-title>
          <v-card-text>
            <!-- –í—ã–±–æ—Ä —Ä–æ–ª–∏ -->
            <v-select
              v-model="selectedRole"
              :items="roles"
              item-title="title"
              item-value="value"
              label="–í—ã–±–µ—Ä–∏—Ç–µ —Ä–æ–ª—å"
              variant="outlined"
              class="mb-4"
            ></v-select>

            <!-- –ö–Ω–æ–ø–∫–∞ –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ -->
            <v-btn 
              color="primary" 
              @click="generateKey"
              :disabled="!selectedRole"
              block
              class="mb-4"
            >
              –°–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞—Ç—å –∫–ª—é—á
            </v-btn>

            <!-- –û—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Å–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞–Ω–Ω–æ–≥–æ –∫–ª—é—á–∞ -->
            <v-card 
              v-if="generatedKey" 
              variant="outlined" 
              class="mb-4"
            >
              <v-card-text>
                <div class="d-flex align-center">
                  <v-chip 
                    :color="getRoleColor(selectedRole)" 
                    class="me-2"
                  >
                    {{ getRoleTitle(selectedRole) }}
                  </v-chip>
                  <span class="text-caption text-medium-emphasis">
                    –†–æ–ª—å
                  </span>
                </div>
                
                <v-divider class="my-3"></v-divider>
                
                <div class="mb-3">
                  <div class="text-subtitle-2 mb-2">–°–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞–Ω–Ω—ã–π –∫–ª—é—á:</div>
                  <v-text-field
                    :model-value="generatedKey"
                    readonly
                    variant="outlined"
                    density="compact"
                    class="key-field"
                  >
                    <template #append-inner>
                      <v-btn
                        icon="mdi-content-copy"
                        size="small"
                        variant="text"
                        @click="copyKey"
                      ></v-btn>
                    </template>
                  </v-text-field>
                </div>

                <!-- –ö–Ω–æ–ø–∫–∏ –¥–µ–π—Å—Ç–≤–∏–π -->
                <div class="d-flex gap-2">
                  <v-btn
                    color="success"
                    variant="outlined"
                    prepend-icon="mdi-content-copy"
                    @click="copyKey"
                  >
                    –ö–æ–ø–∏—Ä–æ–≤–∞—Ç—å
                  </v-btn>
                  
                  <v-btn
                    color="primary"
                    variant="outlined"
                    prepend-icon="mdi-share"
                    @click="shareKey"
                  >
                    –ü–æ–¥–µ–ª–∏—Ç—å—Å—è
                  </v-btn>
                </div>
              </v-card-text>
            </v-card>

            <!-- –£–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ –∫–æ–ø–∏—Ä–æ–≤–∞–Ω–∏–∏ -->
            <v-snackbar
              v-model="showCopyNotification"
              color="success"
              timeout="2000"
            >
              –ö–ª—é—á —Å–∫–æ–ø–∏—Ä–æ–≤–∞–Ω –≤ –±—É—Ñ–µ—Ä –æ–±–º–µ–Ω–∞!
              <template #actions>
                <v-btn
                  variant="text"
                  @click="showCopyNotification = false"
                >
                  –ó–∞–∫—Ä—ã—Ç—å
                </v-btn>
              </template>
            </v-snackbar>

            <!-- –£–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ -->
            <v-snackbar
              v-model="showErrorNotification"
              color="error"
              timeout="3000"
            >
              {{ errorMessage }}
              <template #actions>
                <v-btn
                  variant="text"
                  @click="showErrorNotification = false"
                >
                  –ó–∞–∫—Ä—ã—Ç—å
                </v-btn>
              </template>
            </v-snackbar>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script setup>
import { ref } from 'vue'
import { userApi } from '../../api/user.api.js'

// –†–µ–∞–∫—Ç–∏–≤–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ
const selectedRole = ref('')
const generatedKey = ref('')
const showCopyNotification = ref(false)
const showErrorNotification = ref(false)
const errorMessage = ref('')

// –†–æ–ª–∏
const roles = [
  { title: '–ú–µ–Ω–µ–¥–∂–µ—Ä', value: 'manager' },
  { title: '–†–∞–±–æ—Ç–Ω–∏–∫', value: 'worker' }
]

// –§—É–Ω–∫—Ü–∏—è –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ –∫–ª—é—á–∞
const generateKey = async () => {
  try {
    if (!selectedRole.value) return
  
    const response = await userApi.generateKey(selectedRole.value);

    const data = await response.json();

    if (!response.ok) {
      throw new Error(data.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ –∫–ª—é—á–∞');
    }
    
    generatedKey.value = data.data.key
    
  } catch (error) {
    errorMessage.value = error.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ –∫–ª—é—á–∞'
    showErrorNotification.value = true
  }
}

// –§—É–Ω–∫—Ü–∏—è –∫–æ–ø–∏—Ä–æ–≤–∞–Ω–∏—è –∫–ª—é—á–∞
const copyKey = async () => {
  try {
    await navigator.clipboard.writeText(generatedKey.value)
    showCopyNotification.value = true
  } catch (err) {
    errorMessage.value = '–ù–µ —É–¥–∞–ª–æ—Å—å —Å–∫–æ–ø–∏—Ä–æ–≤–∞—Ç—å –∫–ª—é—á'
    showErrorNotification.value = true
  }
}

// –§—É–Ω–∫—Ü–∏—è –ø–æ–¥–µ–ª–∏—Ç—å—Å—è –∫–ª—é—á–æ–º
const shareKey = async () => {
  const shareData = {
    title: '–ö–ª—é—á –¥–æ—Å—Ç—É–ø–∞',
    text: `–ö–ª—é—á –¥–æ—Å—Ç—É–ø–∞ (${getRoleTitle(selectedRole.value)}): ${generatedKey.value}`,
    url: window.location.href
  }

  try {
    if (navigator.share) {
      await navigator.share(shareData)
    } else {
      // Fallback - –∫–æ–ø–∏—Ä—É–µ–º –≤ –±—É—Ñ–µ—Ä –æ–±–º–µ–Ω–∞
      await navigator.clipboard.writeText(shareData.text)
      showCopyNotification.value = true
    }
  } catch (err) {
    errorMessage.value = '–ù–µ —É–¥–∞–ª–æ—Å—å –ø–æ–¥–µ–ª–∏—Ç—å—Å—è –∫–ª—é—á–æ–º'
    showErrorNotification.value = true
  }
}

// –ü–æ–ª—É—á–∏—Ç—å —Ü–≤–µ—Ç –¥–ª—è —Ä–æ–ª–∏
const getRoleColor = (role) => {
  const colors = {
    manager: 'orange',
    worker: 'blue'
  }
  return colors[role] || 'grey'
}

// –ü–æ–ª—É—á–∏—Ç—å –Ω–∞–∑–≤–∞–Ω–∏–µ —Ä–æ–ª–∏
const getRoleTitle = (role) => {
  const roleObj = roles.find(r => r.value === role)
  return roleObj ? roleObj.title : ''
}
</script>

<style scoped>
.key-field {
  font-family: monospace;
  font-weight: bold;
}
</style>