import { defineStore } from 'pinia'

const SERVER = 'http://localhost:3000';

export const useAuthStore = defineStore('auth', {
  state: () => ({
    user: null,   // { id, email, role }
    token: localStorage.getItem('token') || null,
  }),

  getters: {
    role: (state) => state.user?.role || null,
    isAuthenticated: (state) => !!state.token
  },

  actions: {
    async login({ email, password }) {
      try {
        // —à–∞–≥ 1: –ª–æ–≥–∏–Ω–∏–º—Å—è –∏ –ø–æ–ª—É—á–∞–µ–º —Ç–æ–∫–µ–Ω
        const res = await fetch(`${SERVER}/api/auth/login`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ email, password })
        })

        if (!res.ok) {
          const errorData = await res.json()
          throw new Error(errorData.message || '–ù–µ–≤–µ—Ä–Ω—ã–π –ª–æ–≥–∏–Ω –∏–ª–∏ –ø–∞—Ä–æ–ª—å')
        }

        const { data } = await res.json()
        this.token = data.token
        localStorage.setItem('token', this.token)

        // —à–∞–≥ 2: —Å—Ä–∞–∑—É –ø–æ–¥—Ç—è–≥–∏–≤–∞–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
        await this.fetchMe()
      } catch (error) {
        // –û—á–∏—â–∞–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ –≤ —Å–ª—É—á–∞–µ –æ—à–∏–±–∫–∏
        this.logout()
        throw error
      }
    },

    async register(dto) {
      try {
        // –®–∞–≥ 1: –†–µ–≥–∏—Å—Ç—Ä–∏—Ä—É–µ–º—Å—è
        const res = await fetch(`${SERVER}/api/auth/register`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(dto)
        })

        if (!res.ok) {
          const errorData = await res.json()
          throw new Error(errorData.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏')
        }

        const responseData = await res.json()
        console.log('Registration successful:', responseData)

        // –®–∞–≥ 2: –ê–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ –ª–æ–≥–∏–Ω–∏–º—Å—è —Å —Ç–µ–º–∏ –∂–µ –¥–∞–Ω–Ω—ã–º–∏
        await this.login({
          email: dto.email,
          password: dto.password
        })

        return responseData.data?.userId
      } catch (error) {
        // –û—á–∏—â–∞–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ –≤ —Å–ª—É—á–∞–µ –æ—à–∏–±–∫–∏
        this.logout()
        throw error
      }
    },

    async fetchMe() {
      if (!this.token) return

      try {
        const res = await fetch(`${SERVER}/api/users/me`, {
          headers: { Authorization: `Bearer ${this.token}` }
        })

        if (!res.ok) {
          // –ï—Å–ª–∏ —Ç–æ–∫–µ–Ω –Ω–µ–≤–∞–ª–∏–¥–Ω—ã–π, –æ—á–∏—â–∞–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ
          if (res.status === 401) {
            this.logout()
            throw new Error('–°–µ—Å—Å–∏—è –∏—Å—Ç–µ–∫–ª–∞')
          }
          throw new Error('–ù–µ —É–¥–∞–ª–æ—Å—å –∑–∞–≥—Ä—É–∑–∏—Ç—å –ø—Ä–æ—Ñ–∏–ª—å')
        }

        const { data } = await res.json()
        this.user = data
      } catch (error) {
        // –ï—Å–ª–∏ –æ—à–∏–±–∫–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏, –æ—á–∏—â–∞–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ
        if (error.message.includes('–°–µ—Å—Å–∏—è –∏—Å—Ç–µ–∫–ª–∞')) {
          this.logout()
        }
        throw error
      }
    },

    logout() {
      this.user = null
      this.token = null
      localStorage.removeItem('token')
    },

    // –î–û–ë–ê–í–õ–ï–ù–û: –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è
    async init() {
      if (this.token) {
        try {
          await this.fetchMe()
        } catch (error) {
          console.warn('Failed to restore user session:', error.message)
          // –ù–µ –ø—Ä–æ–±—Ä–∞—Å—ã–≤–∞–µ–º –æ—à–∏–±–∫—É, –ø—Ä–æ—Å—Ç–æ –ª–æ–≥–∏—Ä—É–µ–º
        }
      }
    }
  }
})