import { createRouter, createWebHistory } from "vue-router";

import DashboardPage from './components/DashboardPage.vue'
import TaskManager from './components/tasks/TaskManager.vue';
import UserManager from "./components/users/UserManager.vue";
import LoginPage from './components/LoginPage.vue'
import RegisterPage from "./components/RegisterPage.vue";
import { useAuthStore } from './stores/auth.js'

const routes = [
    {
      path: "/tasks",
      name: "tasks",
      component: TaskManager,
      meta: { 
        requiresAuth: true,
        roles: ['admin', 'manager', 'worker'] // –≤—Å–µ —Ä–æ–ª–∏ –º–æ–≥—É—Ç –≤–∏–¥–µ—Ç—å –∑–∞–¥–∞—á–∏
      }
    },
    {
      path: "/users",
      name: "users",
      component: UserManager,
      meta: { 
        requiresAuth: true,
        roles: ['admin', 'manager'] // —Ç–æ–ª—å–∫–æ –∞–¥–º–∏–Ω—ã –∏ –º–µ–Ω–µ–¥–∂–µ—Ä—ã
      }
    },
    {
      path: "/",
      name: "dashboard",
      component: DashboardPage,
      meta: { 
        requiresAuth: true,
        roles: ['admin', 'manager', 'worker'] // –≤—Å–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏
      }
    },
    {
      path: '/login',
      name: 'login',
      component: LoginPage,
      meta: { 
        hideHeader: true,
        requiresGuest: true // —Ç–æ–ª—å–∫–æ –¥–ª—è –Ω–µ–∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω—ã—Ö
      }
    },
    {
      path: '/register',
      name: 'register',
      component: RegisterPage,
      meta: { 
        hideHeader: true,
        requiresGuest: true // —Ç–æ–ª—å–∫–æ –¥–ª—è –Ω–µ–∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω—ã—Ö
      }
    },
    // –°—Ç—Ä–∞–Ω–∏—Ü–∞ "–î–æ—Å—Ç—É–ø –∑–∞–ø—Ä–µ—â–µ–Ω"
    {
      path: '/forbidden',
      name: 'forbidden',
      component: () => import('./components/ForbiddenPage.vue'),
      meta: { hideHeader: true }
    },
    // –ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –Ω–µ–∏–∑–≤–µ—Å—Ç–Ω—ã—Ö –º–∞—Ä—à—Ä—É—Ç–æ–≤
    {
      path: '/:pathMatch(.*)*',
      redirect: '/'
    }
]

const router = createRouter({
  history: createWebHistory(),
  routes
});

router.beforeEach(async (to, from, next) => {
  const auth = useAuthStore()

  // –ñ–¥—ë–º –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏ –µ—Å–ª–∏ —Ç–æ–∫–µ–Ω –µ—Å—Ç—å, –Ω–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –∑–∞–≥—Ä—É–∂–µ–Ω
  if (auth.token && !auth.user) {
    try {
      await auth.fetchMe()
    } catch (error) {
      console.warn('Failed to fetch user during navigation:', error.message)
    }
  }

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
  if (to.meta.requiresAuth && !auth.isAuthenticated) {
    return next('/login')
  }

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –≥–æ—Å—Ç–µ–≤—ã—Ö —Å—Ç—Ä–∞–Ω–∏—Ü (—Ç–æ–ª—å–∫–æ –¥–ª—è –Ω–µ–∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω—ã—Ö)
  if (to.meta.requiresGuest && auth.isAuthenticated) {
    return next('/')
  }

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ —Ä–æ–ª–µ–π
  if (to.meta.roles && auth.isAuthenticated) {
    const userRole = auth.role
    const allowedRoles = to.meta.roles

    if (!allowedRoles.includes(userRole)) {
      console.warn(`Access denied. User role: ${userRole}, Required roles: ${allowedRoles}`)
      return next('/forbidden')
    }
  }

  next()
})

export default router;